.card {
  display: flex;
  align-items: center;      /* centra verticalmente */
  width: 320px;             /* un poco más ancho */
  margin: 15px 40px;        /* margen más balanceado */
  border: 2px solid #6A0DAD; /* borde con color púrpura */
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(106, 13, 173, 0.3); /* sombra suave y colorida */
  background-color: #1e1e2f; /* fondo oscuro */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.card:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 20px rgba(106, 13, 173, 0.6);
}

.card img {
  width: 80px; /* 5rem = 80px */
  height: 80px;
  object-fit: cover;
  border-radius: 12px;
  margin: 0 15px;
  flex-shrink: 0;
}

.card .lang {
  font-weight: 700;
  font-size: 1.5rem;
  color: #B0B0B0;
  flex-grow: 1;
  user-select: none;
}
