 .card {
          background: #1f1f1f;
          border-radius: 16px;
          width: 280px;
          box-shadow: 0 8px 20px rgba(0, 0, 0, 0.7);
          overflow: hidden;
          transition: transform 0.3s ease, box-shadow 0.3s ease;
          cursor: pointer;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
        }
        .card:hover {
          transform: translateY(-8px);
          box-shadow: 0 12px 30px rgba(0, 0, 0, 0.9);
        }
        .card-image {
          width: 100%;
          height: 170px;
          object-fit: cover;
          border-top-left-radius: 16px;
          border-top-right-radius: 16px;
          filter: brightness(0.85);
          transition: filter 0.3s ease;
        }
        .card:hover .card-image {
          filter: brightness(1);
        }
        .card-content {
          padding: 18px 22px 12px;
          text-align: center;
          color: #ddd;
          font-family: 'Montserrat', sans-serif;
        }
        h2 {
          margin: 0 0 12px;
          font-weight: 700;
          font-size: 1.4rem;
          color: #4fc3f7; /* color azul claro para el nombre */
          letter-spacing: 0.7px;
          text-shadow: 0 0 6px rgba(79, 195, 247, 0.4);
        }
        p {
          margin: 6px 0;
          font-size: 1rem;
          color: #bbb;
        }
        span {
          font-weight: 600;
          color: #888;
        }
        .card-footer {
          border-top: 1px solid #444;
          padding: 10px 22px;
          text-align: center;
          font-size: 0.95rem;
          color: #999;
          font-style: italic;
          letter-spacing: 0.05em;
          user-select: none;
        }
        .card-footer span {
          border-bottom: 2px solid #4fc3f7; /* subrayado azul claro */
          padding-bottom: 2px;
        }