body {
          background-color: #121212;
          margin: 0;
          font-family: 'Montserrat', sans-serif;
          color: #e0e0e0;
        }
        h1 {
          text-align: center;
          margin: 40px 0 30px;
          font-weight: 700;
          font-size: 2.2rem;
          letter-spacing: 1.2px;
          color: #fff;
        }
        .card-list {
          display: flex;
          flex-wrap: wrap;
          gap: 28px;
          justify-content: center;
          max-width: 1200px;
          margin: 0 auto 50px;
          padding: 0 20px;
        }